<template>
  <el-container style="height: 100vh; background: #f9f9f9;">
    <!-- 左侧设置区域 -->
    <el-aside width="50%" class="left-panel">
      <el-card shadow="hover" class="settings-card">
        <h3>基本设置</h3>
        <el-divider></el-divider>

        <!-- Telegram 配置 -->
        <h4>Telegram 配置</h4>
        <el-form-item label="机器人 Token">
          <el-input
              v-model="UpdateSysCfgParams.tgBotToken"
              placeholder="请输入 Telegram Bot Token"
              clearable
              type="password"
              show-password
          />
        </el-form-item>
        <el-form-item label="个人 ID">
          <el-input
              v-model="UpdateSysCfgParams.tgChatId"
              placeholder="请输入 Telegram 个人 ID"
              clearable
              type="password"
              show-password
          />
        </el-form-item>

        <!-- 钉钉配置 -->
        <h4>钉钉配置</h4>
        <el-form-item label="Access Token">
          <el-input
              v-model="UpdateSysCfgParams.dingToken"
              placeholder="请输入钉钉 Access Token"
              clearable
              type="password"
              show-password
          />
        </el-form-item>
        <el-form-item label="Secret">
          <el-input
              v-model="UpdateSysCfgParams.dingSecret"
              placeholder="请输入钉钉 Secret"
              clearable
              type="password"
              show-password
          />
        </el-form-item>

        <!-- 版本更新通知配置 -->
        <h4>版本更新通知配置</h4>
        <el-form-item label="是否启用版本更新通知">
          <el-switch
              v-model="UpdateSysCfgParams.enableVersionInform"
              active-text="启用"
              inactive-text="禁用"
          />
        </el-form-item>

        <!-- 每日播报配置 -->
        <h4>每日播报配置</h4>
        <el-form-item label="是否启用每日播报">
          <el-switch
              v-model="UpdateSysCfgParams.enableDailyBroadcast"
              active-text="启用"
              inactive-text="禁用"
          />
        </el-form-item>
        <el-form-item label="定时任务cron表达式">
          <el-input
              v-model="UpdateSysCfgParams.dailyBroadcastCron"
              placeholder="例（每天早晨9点30分0秒）：0 30 9 * * ?"
              clearable
          />
        </el-form-item>

        <!-- MFA 配置 -->
        <h4>MFA 配置</h4>
        <el-form-item label="启用 MFA">
          <el-switch
              v-model="UpdateSysCfgParams.enableMfa"
              active-text="启用"
              inactive-text="禁用"
          />
        </el-form-item>
        <p v-if="UpdateSysCfgParams.enableMfa" class="mfa-tip">
          ⭐ 如果禁用后重新启用，请在 APP 上删除旧验证码并重新扫码绑定新验证码。
        </p>
        <div v-if="UpdateSysCfgParams.enableMfa" class="mfa-preview">
          <el-image
              :src="UpdateSysCfgParams.mfaQrData"
              fit="contain"
              style="width: 200px; height: 200px;"
          />
          <el-input
              v-model="UpdateSysCfgParams.mfaSecret"
              style="margin-top: 10px;"
              disabled
          />
        </div>

        <!-- 提交按钮 -->
        <div class="button-group">
          <el-button type="primary" @click="updateSysCfg" :loading="cfgLoading">更新配置</el-button>
        </div>
      </el-card>
    </el-aside>

    <!-- 右侧测试消息区域 -->
    <el-main class="right-panel">
      <el-card shadow="hover" class="test-card">
        <h3>测试消息发送</h3>

        <!-- 输入框 -->
        <el-input
            type="textarea"
            v-model="message"
            placeholder="请输入测试文本..."
            :rows="10"
            resize="none"
        />

        <!-- 测试按钮 -->
        <div class="button-group">
          <el-button type="primary" @click="sendMsg" :loading="sendLoading">发送测试消息</el-button>
        </div>

        <el-divider></el-divider>
        <h3>测试MFA验证码</h3>

        <!-- 输入框 -->
        <el-input
            v-model="mfaCode"
            clearable
            placeholder="请输入MFA验证码"
        />

        <!-- 测试按钮 -->
        <div class="button-group">
          <el-button type="primary" @click="checkMfaCode" :loading="sendMfaCodeLoading">验证</el-button>
        </div>
      </el-card>
    </el-main>
  </el-container>
</template>


<script>
import {ElMessage} from "element-plus";

export default {
  data() {
    return {
      cfgLoading: false,
      sendLoading: false,
      sendMfaCodeLoading: false,
      UpdateSysCfgParams: {
        dingToken: '',
        dingSecret: '',
        tgChatId: '',
        tgBotToken: '',
        enableMfa: false,
        mfaSecret: '',
        mfaQrData: '',
        enableDailyBroadcast: true,
        dailyBroadcastCron: '',
        enableVersionInform: true,
      },
      message: '',
      mfaCode: ''
    };
  },
  mounted() {
    this.getSysCfg()
  },
  methods: {
    async getSysCfg() {
      await this.$axios.post('/sys/getSysCfg', {})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.UpdateSysCfgParams = response.data.data
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async updateSysCfg() {
      this.cfgLoading = true
      await this.$axios.post('/sys/updateSysCfg', {...this.UpdateSysCfgParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.getSysCfg()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.cfgLoading = false
    },
    async sendMsg() {
      this.sendLoading = true
      await this.$axios.post('/sys/sendMsg', {message: this.message})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.sendLoading = false
    },
    async checkMfaCode() {
      this.sendMfaCodeLoading = true
      await this.$axios.post('/sys/checkMfaCode', {mfaCode: this.mfaCode})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.sendMfaCodeLoading = false
    }
  }
};
</script>

<style scoped>
.left-panel {
  background-color: #ffffff;
  padding: 20px;
}

.settings-card {
  padding: 20px;
}

.right-panel {
  padding: 20px;
}

.test-card {
  padding: 20px;
}

.mfa-tip {
  color: #ff8c00;
  font-size: 14px;
}

.mfa-preview {
  margin-top: 10px;
}

.button-group {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px
}
</style>

